.home_outputIpsum__1CJug textarea {
  font-size: 1rem;
  line-height: 32px;
  border: 1px solid #8F5F00;
  border-radius: 10px;
  padding: 2rem 1.5rem;
  white-space: pre-wrap;
  background-color: #DEEFEC;
  width: 100%;
  overflow-y: auto;
  /* overflow-y: scroll; */
  height: 250px;
  resize: none;
  font-family: inherit;
}

textarea ::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}

textarea ::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, .5);
  box-shadow: 0 0 1px rgba(255, 255, 255, .5);
}

.home_outputIpsum__1CJug button {
  position: absolute;
  font-weight: 600;
